import Vue from "vue";
import router from "@/router";
import axios from "axios";
export default {
  namespaced: true,
  state: {
    list: [],
    loading: false,
  },
  mutations: {
    set_list(state, list) {
      state.list = list;
    },
    append(state, data) {
      let list = state.list;
      list.push(data);
      state.list = list;
    },
  },
  actions: {
    get_list({ state, commit }) {
      axios.get("http://185.105.2.10:3000/v1/images/").then((data) => {
        console.log("list", data.data);
        commit("set_list", data.data);
      });
    },
    async new_file({ state, commit }, { file }) {
      try {
        const formData = new FormData();
        formData.append("image", file);
        formData.append("user_id", "1");
        axios
          .post("http://185.105.2.10:3000/v1/images/load", formData)
          .then((data) => {
            console.log(data.data, "data");
            commit("append", data.data);
          });
      } catch (e) {
        console.log(e, "e");
      }
      //upload code here
      // upload file to server
      // create task
    },
  },
};
