<template>
  <div class="hello vtb-cool-header flex flex-row items-center">
    <img class="delo_icon" src="@/assets/blue-logo.svg" alt="" />
    <div class="title ml-6">AI Удаление фона</div>
    <!-- <img class="vtb_icon ml-8" src="@/assets/vtb.svg" alt="" /> -->
    <div class="flex"></div>
    <div class="button_thing">
      <button
        v-if="!loading"
        class="mr-6 bg-blue-500 hover:bg-blue-700 text-white font-bold upload_button py-2 px-4 rounded focus:outline-none focus:shadow-outline"
        type="button"
      >
        <input
          type="file"
          @change="pre_upload"
          id="image_file_input"
          accept="image/*"
        />
        Добавить файл
      </button>
      <img
        v-if="loading"
        class="loading_icon mr-6"
        src="@/assets/loader.svg"
        alt=""
      />
    </div>
  </div>
</template>

<script>
/* eslint-disable */

import { mapState } from "vuex";

export default {
  name: "sample",
  props: {},
  computed: {
    ...mapState({
      // list: state => state.graphs.list
    })
  },
  data() {
    return {
      loading: false
    };
  },
  created() {},
  methods: {
    pre_upload(event) {
      // let file = event.target.files[0];
      // console.log(file);
      this.$data.loading = true
      for (let i = 0; i < event.target.files.length; i++) {
        this.$store.dispatch("upload/new_file", { file: event.target.files[i] });
      }
      setTimeout(()=>{
        this.$data.loading = false
      }, 10000)
    }
  },
  components: {}
};
</script>

